import React from 'react';
import {genPromise} from '../plugins';
import ResultView from './result.jsx';
import Header from './header'
export class ProjectCard extends React.Component{
    render(){
        return(
            <div className='card'>
                <div className="card-body">
                    <span className="badge">{this.props.tag}</span>
                    <h5 className="card-title">
                        {this.props.title}
                        
                    </h5>
                    <p className="card-text">{this.props.abstract}</p>
                    {this.props.link&&<a className='btn btn-self' href={this.props.link}>Link</a>}
                </div>
            </div>
        )
    }
}

export default class Project extends React.Component{
    constructor(props){
        super(props);
        this.state={
            project_list:[],
            load:false,
            error:false,
            error_msg:'error'
        }
    }
    componentDidMount(){
        genPromise('GET','http://localhost/info/project/project.php').then(
            (data)=>{
                if(data.code===0){
                    this.setState({
                        project_list:data.data,
                        load:true
                    })
                }else{
                    this.setState({
                        error:true
                    })
                }
            },(error)=>{
                console.log(error);
                this.setState({
                    error:true,
                    error_msg:error.message
                })
            }
        )
    }

    render(){
        const project_list=this.state.project_list.map((el)=>
            <ProjectCard key={el.id}
                         title={el.title}
                         abstract={el.abstract}
                         tag={el.tag}
                         link={el.link} />
        );
        const ContentLoader=()=>
                (<div className='content-loader p-2'>
                    <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                        style={{width:'85%',height:'.8rem'}}></div>
                    <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                        style={{width:'60%',height:'.8rem'}}></div>
                    <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                        style={{width:'60%',height:'.8rem'}}></div>
                </div>);
        if(this.state.error){
            return(
                <div><Header />
                <div className='container project-container'>
                    
                    <ResultView text={this.state.error_msg} />
                </div></div>
            )
        }
        else if(this.state.load){
            return(
                <div> <Header />
                <div className='container project-container'>
                   
                    <h4 className='title border-bottom'>项目</h4>
                    
                    {project_list}
                </div></div>
            );
        }else{
            return(
                <div><Header />
                <div className='container project-container'>
                    
                        <div className='conent-loader-rect rounded-pill bg-grey animated-bg m-3'
                            style={{width:'25%',height:'.8rem'}}></div>
                        <ContentLoader />
                        <ContentLoader />
                        <ContentLoader />
                    </div></div>
                
            )
        }
        
    }
}